<template>
  <div class="gpt">
    <div class="nav_nav">
      <div class="home_top_title">Model: {{ modelList[modelIndex] }}</div>
      <div class="home_top_left">
        <img @click="menuShow = !menuShow" src="../assets/set.jpg" alt="" />
        <button @click="goClear">清空</button>
      </div>
    </div>
    <div class="chatView">
      <div v-for="(item, index) in chatList" :key="index">
        <template v-if="item.isUser">
          <div class="c-user">
            <img src="../assets/logo.png" alt="" />
            <div>{{ item.content }}</div>
          </div>
        </template>
        <template v-else>
          <div class="c-user" style="background: #dcdfe6">
            <img src="../assets/gpt.jpg" alt="" />
            <div>{{ item.content }}</div>
          </div>
        </template>
      </div>
    </div>
    <div class="bottomView">
      <input v-model="inputText" type="text" />
      <button @click="send">发送</button>
    </div>
    <template v-if="menuShow">
      <div class="shadow">
        <div class="menuBg">
          <div>
            <h3 class="modelTitle">Model</h3>
            <div class="modelBtn">
              <button
                v-for="(item, index) in modelList"
                :key="index"
                :class="modelIndex == index ? 'selBtn' : ''"
                @click="modelIndex = index"
              >
                {{ item }}
              </button>
            </div>
          </div>
          <div>
            <h3 class="modelTitle">Token List</h3>
            <div class="modelBtn">
              <button
                v-for="(item, index) in maxTokenList"
                :key="index"
                :class="maxTokenIndex == index ? 'selBtn' : ''"
                @click="maxTokenIndex = index"
              >
                {{ item }}
              </button>
            </div>
          </div>
          <div>
            <h3 class="modelTitle">Url Line</h3>
            <div class="modelBtn">
              <button
                v-for="(item, index) in postUrl"
                :key="index"
                :class="postIndex == index ? 'selBtn' : ''"
                @click="
                  postIndex = index;
                  useInputUrl = false;
                "
              >
                {{ item }}
              </button>
            </div>
            <div class="modelUrl">
              <input
                v-model="inputUrl"
                type="text"
                placeholder="输入posturl"
                :class="useInputUrl ? 'selBtn' : ''"
              />
              <button @click="useUrl" :class="useInputUrl ? 'selBtn' : ''">
                使用
              </button>
            </div>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      menuShow: false,
      inputText: "",
      chatList: [],
      chatModel: {
        isUser: false,
        content: "",
        id: 0,
      },
      chatID: 0,
      chatgptModel: "智障机器人",
      modelList: [
        "text-embedding-ada-002",
        "text-davinci-003",
        "davinci",
        "curie",
        "babbage",
        "ada",
        "Prometheus",
      ],
      modelIndex: 1,
      maxTokenList: [100, 500, 1000, 2000, 5000, 0],
      maxTokenIndex: 1,
      postUrl: [
        "https://api.openai.com/v1/engines/davinci/jobs",
        "https://api.openai.com/v1/completions",
      ],
      postIndex: 1,
      useInputUrl: false,
      inputUrl: "",
    };
  },
  watch: {},
  methods: {
    goClear() {
      this.chatList = [];
      this.chatID = 0;
    },
    useUrl() {
      this.useInputUrl = !this.useInputUrl;
      if (this.useInputUrl) {
        this.postIndex = -1;
      } else {
        this.postIndex = 0;
      }
    },
    async send() {
      if (this.inputText == "") {
        return;
      }

      let chatModel = {
        isUser: true,
        content: this.inputText,
        id: this.chatID,
      };
      this.chatList.push(chatModel);
      this.chatID++;
      this.chatList.forEach((element) => {
        // console.log("----", element.id);
      });
      await this.generateText(this.inputText).then(async (res) => {
        let chatModel2 = {
          isUser: false,
          content: res,
          id: this.chatID,
        };
        this.inputText == "";
        this.chatList.push(chatModel2);
        this.chatID++;

        this.chatList.forEach((element) => {
          // console.log("====", element.id);
        });
      });
    },
    async generateText(prompt) {
      console.log("text=", prompt);
      let url = this.postUrl[this.postIndex];
      if (this.useInputUrl) {
        url = this.inputUrl;
      }

      try {
        const response = await axios.post(
          url,
          {
            prompt: prompt,
            model: this.modelList[this.modelIndex],
            max_tokens: this.maxTokenList[this.maxTokenIndex],
            temperature: 0.5,
          },
          {
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer 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`,
            },
          }
        );
        console.log(response.data.choices[0].text);
        return response.data.choices[0].text;
      } catch (error) {
        console.error(error);
      }
    },
  },
};
</script>

